
import Vue from 'vue';
import mixins from 'vue-typed-mixins'

import BaseComponent from './BaseComponent.vue';
import showdown from 'showdown'


const converter = new showdown.Converter()

export default mixins(BaseComponent).extend({
    name: 'Showdown',
    data() {
        return {
            markdown_text: '',
        }
    },
    props: {
        markdown: {
            type: String,
            default: null
        },
        src: {
            type: String,
            default: null
        }
    },
    watch: {
        src() {
            this.load()
        },
        markdown() {
            this.set_markdown(this.markdown)
        }
    },
    mounted() {
        if (this.src !== null) {
            this.load()
        } else if (this.markdown !== null) {
            this.set_markdown(this.markdown)
        }
    },
    methods: {
        set_markdown(markdown: string) {
            let dom = converter.makeHtml(markdown);
            this.markdown_text = markdown;
            (this.$refs.content as HTMLElement).innerHTML = dom;
        },
        async load() {
            try {
                let text = await this.load_text(this.src)
                this.set_markdown(text);
            } catch (err) {
                console.error(err)
            }
        }
    },
});
